import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Box, VStack, Text, Icon, Input, InputGroup, InputLeftElement, Button, Flex, Avatar } from "@chakra-ui/react";
import { LuSquareCheck } from "react-icons/lu";
import { FaShoppingCart, FaHeart, FaUser, FaSearch } from "react-icons/fa";
import { IoShirt } from "react-icons/io5";

const tabInicial = () => {
  const router = useRouter();
  const [searchTerm, setSearchTerm] = useState("");
  const [sidebarSection, setSidebarSection] = useState("loja");

  useEffect(() => {
    const token = localStorage.getItem("authToken");
    if (!token) {
      router.replace("/Login");
    }
  }, [router]);

  return (
    <Box minH="100vh" display="flex" flexDirection="column" style={{ background: "rgba(30, 43, 104, 0.97)" }}>
      {/* Header estilo Netshoes */}
      <Flex
        w="100%"
        bg="#8000FF"
        py={2}
        px={8}
        align="center"
        justify="space-between"
        gap={4}
      >
        {/* Logo ou título */}
        <Text fontWeight="bold" fontSize="2xl" color="white" fontStyle="italic" letterSpacing={1}>
          netshoes
        </Text>

        {/* Barra de pesquisa */}
        <InputGroup maxW="600px" flex="1" mx={8}>
          <InputLeftElement pointerEvents="none">
            <FaSearch color="#8000FF" />
          </InputLeftElement>
          <Input
            bg="white"
            borderRadius="full"
            placeholder="O que você está procurando?"
            value={searchTerm}
            onChange={e => setSearchTerm(e.target.value)}
            pl={10}
            fontSize="lg"
            _placeholder={{ color: "gray.500" }}
          />
        </InputGroup>

        {/* Ícones à direita */}
        <Flex align="center" gap={6}>
          <Flex align="center" color="white" cursor="pointer" _hover={{ color: "yellow.300" }}>
            <FaHeart />
            <Text ml={2} fontSize="md" display={["none", "block"]}>Lista de Desejos</Text>
          </Flex>
          <Button
            leftIcon={<FaUser />}
            colorScheme="whiteAlpha"
            variant="ghost"
            color="white"
            _hover={{ bg: "whiteAlpha.300" }}
            borderRadius="full"
          >
            Entrar
          </Button>
          <Box position="relative" color="white" cursor="pointer">
            <FaShoppingCart size={22} />
            <Box
              position="absolute"
              top="-8px"
              right="-10px"
              bg="yellow.300"
              color="black"
              fontSize="xs"
              fontWeight="bold"
              borderRadius="full"
              px={2}
              py={0.5}
            >
              0
            </Box>
          </Box>
        </Flex>
      </Flex>

      {/* Resto da página */}
      <Box flex="1" display="flex">
        {/* Sidebar permanente */}
        <Box
          w="220px"
          color="white"
          p={6}
          display="flex"
          flexDirection="column"
          alignItems="center"
          boxShadow="md"
          bgGradient="linear(to-t, blue.700, black)"
        >
          <Text fontWeight="bold" fontSize="xl" mb={8}>
            Menu
          </Text>
          <VStack spacing={6} align="stretch" w="100%">
            <Box
              as="button"
              w="100%"
              textAlign="left"
              py={2}
              px={4}
              borderRadius="md"
              bg={sidebarSection === "loja" ? "blue.600" : "transparent"}
              _hover={{ bg: "blue.600" }}
              onClick={() => setSidebarSection("loja")}
            >
              <Icon as={IoShirt} mr={2} />
              Loja
            </Box>
            <Box
              as="button"
              w="100%"
              textAlign="left"
              py={2}
              px={4}
              borderRadius="md"
              bg={sidebarSection === "carrinho" ? "blue.600" : "transparent"}
              _hover={{ bg: "blue.600" }}
              onClick={() => setSidebarSection("carrinho")}
            >
              <Icon as={FaShoppingCart} mr={2} />
              Carrinho
            </Box>
            <Box
              as="button"
              w="100%"
              textAlign="left"
              py={2}
              px={4}
              borderRadius="md"
              bg={sidebarSection === "config" ? "blue.600" : "transparent"}
              _hover={{ bg: "blue.600" }}
              onClick={() => setSidebarSection("config")}
            >
              <Icon as={LuSquareCheck} mr={2} />
              Configurações
            </Box>
          </VStack>
        </Box>

        {/* Conteúdo principal */}
        <Box flex="1" p={8} display="flex" flexDirection="column" alignItems="center">
          {/* Aqui você pode colocar o conteúdo da loja, carrinho, etc */}
          {sidebarSection === "loja" && (
            <Text color="white" fontSize="2xl" mt={10}>
              Conteúdo da Loja
            </Text>
          )}
          {sidebarSection === "carrinho" && (
            <Text color="white" fontSize="2xl" mt={10}>
              Carrinho principal selecionado!
            </Text>
          )}
          {sidebarSection === "config" && (
            <Text color="white" fontSize="2xl" mt={10}>
              Configurações selecionado!
            </Text>
          )}
        </Box>
      </Box>
    </Box>
  );
};

export default tabInicial;